import React from 'react'

export default function Example() {
  return (
    <div className="bg-white px-6 py-24 lg:px-8">
      <div className="mx-auto max-w-3xl text-base leading-7 text-gray-700">
        <a
          href="/notice"
          className="float-right mb-10 block text-base font-semibold leading-7 text-gray-400 hover:underline focus:underline focus:outline-none"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth={2}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M10 19l-7-7m0 0l7-7m-7 7h18"
            />
          </svg>
        </a>
        <p className="text-base font-semibold leading-7 text-gray-400">
          공지사항
        </p>
        <h1 className="mt-2 text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
          4월 당일예약 30% 할인 이벤트
        </h1>
        <div className="mt-6 max-w-2xl">
          <figure className="mt-10 border-l border-gray-400 pl-9"></figure>
          <p className="mt-10">
            안녕하세요! 저희 펜션에서는 4월 내 당일 예약하시는 고객님들께 특별한
            혜택을 제공해 드리고자 합니다. 4월 내 당일 예약하시는 고객님들께는
            30% 할인 혜택을 드립니다. 이번 이벤트는 당일 예약에만 적용되며, 예약
            시에 이벤트 참여를 신청하시면 자동으로 할인이 적용됩니다.
          </p>
        </div>
        <figure className="mt-16">
          <img
            className="aspect-video rounded-xl bg-gray-50 object-cover"
            src="/img/popup2.png"
            alt=""
          />
        </figure>
      </div>
    </div>
  )
}
